.search-result-card{
    display: flex;
    width: 30rem;
    height: 12rem;
    margin-top: 40px;
    background-color: #4e4e4e;
    border-radius: 20px;
    color: white;
    font-size: 15px;
}
.thumbnail{
    height: 100%;
    aspect-ratio: unset;
    border-radius: 20px 0px 0px 20px;

}
.video-info{
    width: 40%;
    height: 100%;
}
.video-info h3{
    font-size: 19px;
    margin: 14px 10px;


}
.video-info p{
    font-size: 13px;
    margin: 14px 10px;

}
