body{
    font-family: "Montserrat", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

.MovieTrendBox{
    height: 70vh;
    width: 100%;
    display: flex;
    position: relative;
    align-items: center;
    background-size: cover;
    background-repeat: no-repeat;
    transition: 0.1s ease-in;
}
.MovieTrendBox::before{
    content: '';
    background-color: rgba(0, 0, 0, 0.664);
   z-index: 400;
   position: absolute;
   top: 0px;
   left: 0px;
   height: 100%;
   width: 100%;
}

.TitleBox{
    display: flex;
        flex-direction: column;
        width: 30%;
        height: 100%;
        color: white;
        z-index: 600;
        margin: 14rem 0rem 0rem 5rem;
        align-items: start;
        }
         h1{
            transition: 0.1s ease-in;
            font-size: 30px;
            line-height: 41px;
            letter-spacing: 1px;
            color: white;}

           .TitleBox caption{
            color: #e8dfdf;
            font-size: 17px;
            line-height: 25px;
            letter-spacing: 0.5px;
        

            }

.CardHolder{
    z-index: 1000;
    display: flex
;
    width: 70%;
    position: relative;
   
    overflow: scroll;
    margin: 0px 20px 0rem 0rem;
}
.CardHolder1{
    z-index: 1000;
    display: flex
;flex-direction: row-reverse;
    width: 70%;
    position: relative;
   
    overflow: scroll;
    margin: 0px 20px 0rem 0rem;
}


