.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
    background-color:black;
    color: #fff;
    box-shadow: 0px 0px 52px -11px #313030;}
  
  .logo {
    display: flex;
    align-items: center;
  }
  
  .logo img {
    height: 2rem;
  }
  
  .nav-links {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .nav-links li {
    margin-right: 1.5rem;
  }
  
  .nav-links a {
    color: #fff;
    text-decoration: none;
  }
  
  .search-bar {
    display: flex;
    align-items: center;
    position: relative;
  }
  
  .search-bar input {
    padding: 0.5rem;
    border: none;
    border-radius: 25px;
  }
  .search-bar a{    position: absolute;
    right: 9px;
   
    width: 17%;
    height: 100%;}
  .search-bar button {
    background-color: #444;
    color: #fff;
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 4px;
    cursor: pointer;
    background: none;
    position: absolute;
    right: 0px;
  }
  
  .upload-button {
    margin-left: 1rem;
  }
  
  .upload-button button {
    background-color: #28a745;
    color: #fff;
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .profile-icon a {
    color: #fff;
    font-size: 1.5rem;
  }